.dot:nth-last-child(3){-webkit-animation:loading .6s .1s linear infinite;animation:loading .6s .1s linear infinite}
@-webkit-keyframes loading{0%{-webkit-transform:translate(0,0);transform:translate(0,0);background-color:#ababab}25%{-webkit-transform:translate(0,-3px);transform:translate(0,-3px)}50%{-webkit-transform:translate(0,0);transform:translate(0,0);background-color:#ababab}75%{-webkit-transform:translate(0,3px);transform:translate(0,3px)}100%{-webkit-transform:translate(0,0);transform:translate(0,0)}}
@keyframes loading{0%{-webkit-transform:translate(0,0);transform:translate(0,0);background-color:#ababab}25%{-webkit-transform:translate(0,-3px);transform:translate(0,-3px)}50%{-webkit-transform:translate(0,0);transform:translate(0,0);background-color:#ababab}75%{-webkit-transform:translate(0,3px);transform:translate(0,3px)}100%{-webkit-transform:translate(0,0);transform:translate(0,0)}}